import React from "react"
import { Link, graphql } from "gatsby"

import Bio from "../components/bio"
import Layout from "../components/layout"
import SignupForm from "../components/signup-form"
import SEO from "../components/seo"
// import LandingPageBioDetails from "../components/LandingPageBioDetails";
import { rhythm } from "../utils/typography"

class SiteIndex extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title
    const posts = data.allMarkdownRemark.edges

    let filtered = [];
    for (let i = 0; i < posts.length; i++) {
        if (posts[i].node.frontmatter.listing !== "false") {
            filtered.push(posts[i]);
        }
    }

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO
          title="Anubhav Singh"
          keywords={[`blog`, `anubhav`, `singh`, `xprilion`, `xpri`, `developer`, `dynopii`, `founder`, `entrepreneur`, `kolkata`, `india`, `allahabad`]}
        />
        <div style={{
          maxWidth: "800px",
          marginLeft: "auto",
          marginRight: "auto"
        }}>
          <Bio />
          {/* <hr></hr>
          <LandingPageBioDetails /> */}
          <hr></hr>
          {filtered.slice(0, 3).map(({ node }) => {
            if (node.frontmatter.listing !== "false"){
              const title = node.frontmatter.title || node.fields.slug
              return (
                <div key={node.fields.slug}>
                  <h3
                    style={{
                      marginBottom: rhythm(1 / 4),
                      fontWeight: "normal"
                    }}
                  >
                    <Link style={{ boxShadow: `none` }} to={node.fields.slug}>
                      {title}
                    </Link>
                  </h3>
                  <small>{node.frontmatter.date}</small>
                  <p
                    dangerouslySetInnerHTML={{
                      __html: node.frontmatter.description || node.excerpt,
                    }}
                  />
                </div>
              )
            }
          })}
          <Link
            style={{
              boxShadow: `none`,
              textDecoration: `none`,
              color: `inherit`,
              padding: "0.5em 1em",
              border: "2px solid #5c5c5c",
              borderRadius: "4px",
            }}
            to={`/posts/`}
          >
            More â†’
          </Link>
          <br></br>
          <br></br>
          <SignupForm />
        </div>
      </Layout>
    )
  }
}

export default SiteIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            listing
          }
        }
      }
    }
  }
`
